var map = {
  "0":"",
  "1":"swap",
  "2":"",
  "3":"inode",
  "4":"",
  "5":"binlog",
  "6":"",
  "7":"myisam",
  "8":"",
  "9":"",
  "10":"",
  "11":"",
  "12":"",
  "13":"",
  "14":"",
  "15":""
}
var mapCom = {
  "0":"",
  "1":"",
  "2":"",
  "3":"",
  "4":"(M)",
  "5":"(M)",
  "6":"",
  "7":"1myisam 0",
  "8":"(M)",
  "9":"",
  "10":"",
  "11":"1 0",
  "12":"1 0",
  "13":"1 0",
  "14":"1 0",
  "15":"1  0"
}
export default {
  data() {
    return {
      hostConList: [],
      sqlConList: [],
      isUpdate: -1,
      isUpdateMysql:-1
    }
  },
  filters: {
    toHostText:function (value) {
      return map[value] || ''
    },
    toHostCom:function (value) {
      return mapCom[value] || ''
    },
  },
  mounted() {
    this.getConfigList()
  },
  directives: {
    focus: {
      // 
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    selTxt:function(index){
      document.getElementById('#'+index)
    },
    //
    upModify:function(host){
      this.isUpdate=-1;
      this.isUpdateMysql=-1;
      // console.log(host)
      host.threshold = parseInt(host.threshold)
      // console.log(JSON.stringify(host))
      this.$http.post('/acmeCloud/inspection/config/modify',
        JSON.stringify(host), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("!")
          this.getConfigList()
        } else {
          // 
          // console.log("...")
          alert("!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //
    getConfigList: function () {
      this.$http.get('/acmeCloud/inspection/config/list').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 
          this.hostConList = response_info.data.host_inspection_conf;
          this.sqlConList = response_info.data.mysql_inspection_conf
          // console.log(this.hostConList);
          // console.log(this.sqlConList)
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      })
    }
  }
}



