export default {
  data() {
    return {
      accountName: "",
      accountPassword: "",
      tel: "",
      email: "",
      role_id: "",//id
      role_name:"",  //
      role_name_alias:"",//
      saveRoleId:3,//id
      saveEmail:"",//
      saveTel: "",
      saveName:"",
      savePasswd:"",
      oldPassword: "",   //
      newPassword: "",   //
      confirmNewPassword: "",   //
      account_name:"",
      account_id:this.$cookies.get("account_id"),//ID
      total: 0,       //
      currentPage: 1,   //
      current: 1,  //
      accountList:[],
      userList:[],
      deleteAccountId:"",//id
      deleteName:"",//
      // upUpUser:"",//
      upUser:"",
      upAccountId:"",
      upRole_id:"",   //
      upUserName:"",
      upEmail:"",
      upTel:"",
    }
  },
  mounted(){
    this.getAccountInfo(this.account_id);
    this.setCurrent(1);
    // console.log(this.$cookies.get("account_id"))
  },
  props: {
    // total: {// 
    //   type: Number,
    //   default: 10
    // },
    display: {// 
      type: Number,
      default: 10
    },
    // currentPage: {// 
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods: {
    //
    addNewUser: function () {
      let data = {
        account_name: this.saveName,
        account_password: this.savePasswd,
        tel: this.saveTel,
        email: this.saveEmail,
        role_id: this.saveRoleId
      }
      // console.log(data)
      this.$http.post('/acmeCloud/account/add',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("!")
          // location.reload(true);//
          this.setCurrent(1);
        } else {
          // 
          alert("DBA!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    upThisUser:function () {  //
      this.upAccountId=this.account_id
      this.upRole_id=this.role_id;
      this.upUserName=this.account_name
      this.upTel=this.tel
      this.upEmail=this.email;
    },
    updateAssValue:function(user){  //
      this.upAccountId=user.account_id
      this.upRole_id=user.role_id;
      this.upUserName=user.account_name
      this.upTel=user.tel
      this.upEmail=user.email;
    },

    // 
    updateModify:function () {
      let data={
          account_id:this.upAccountId,
          tel:this.upTel,
          email:this.upEmail,
          account_name:this.upUserName,
          role_id:this.upRole_id
        }

      // console.log(data)
      this.$http.post('/acmeCloud/account/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("!")
          this.getAccountInfo(this.account_id);
          this.setCurrent(1);
        } else {
          // 
          alert("DBA!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //
    getAccountInfo:function (account_id) {
      var param="?account_id="+account_id;
      this.$http.get('/acmeCloud/account/info'+param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 
          this.account_id=response_info.data.account_id;
          this.account_name=response_info.data.account_name;
          this.tel=response_info.data.tel;
          this.email=response_info.data.email;
          this.role_id=response_info.data.role_id;
          this.role_name=response_info.data.role_name;
          this.role_name_alias=response_info.data.role_name_alias

        } else {
          // 
          // console.log("...")
        }
      })
    },
    //
    updateUserPassword: function () {
      return;
      let data = {
        "account_id": this.account_id,
        "old_password": this.oldPassword,
        "new_password": this.newPassword
      }
      // console.log(data)
      this.$http.post('/acmeCloud/account/passwordmodify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("!")
        } else {
          // 
          alert("DBA!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //
    deleteUser: function (accountId) {
      let data = {
        "account_id": accountId,
      }
      // console.log(this.account_id);
      // console.log(data)
      this.$http.post('/acmeCloud/account/delete',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("!")
          this.getAccountInfo(this.account_id);
          this.setCurrent(1);
        } else {
          // 
          alert("DBA!")
        }
      }, function (res) {
        // console.log(res)
      });
    },

    // 
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.currentPage = this.current;
        this.$emit('pagechange', this.current);
      }
      //
      this.$http.get('/acmeCloud/account/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        // console.log(res)
        if (response_info.status == 0) {
          // 
          this.total = response_info.data;

        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
      //   
      let data = {
        "currentPage": this.currentPage,
        "pageSize": this.display
      }
      // console.log(data)
      this.$http.get('/acmeCloud/account/list?'+
        "currentPage="+this.currentPage+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 
          this.userList = response_info.data;
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //
    blurFn: function () {
      // 
      // 

      var newPwd = this.newPassword;
      var configPwd = this.confirmNewPassword;
      //  6~15
      var reg1 = /^([\w\.\_]|[\u4e00-\u9fa5]){6,15}$/;
      if (newPwd == "") {
        ts1.innerHTML = "";
        ts1.style.color = "red";
      }
      else if (reg1.test(newPwd)) {
        ts1.innerHTML = "";
        ts1.style.color = "green";
      }
      else {
        ts1.innerHTML = "6-15";
        ts1.style.color = "red";
        this.newPassword = "";
      }
      ;
      //
      document.getElementById("configPwd").onblur = function () {
        // console.log(configPwd)
        if (configPwd == "") {
          ts2.innerHTML = "";
          ts2.style.color = "red";
        }
        else if (configPwd == newPwd) {
          ts2.innerHTML = "";
          ts2.style.color = "green";
        }
        else {
          ts2.innerHTML = "";
          ts2.style.color = "red";
          this.confirmNewPassword = "";
        }
      }
    }
  }
}



