var map = {
  "0": "",
  "1": "",
  "2": "",
  "3": "",
  "4": "",
  "5": "",
  "6": ""
}
var status = {   //status
  "0": "",
  "1": "",
  "2": "",
  "3": "",
  "4": "",
  "5": "",
  "6": ""
}
export default {
  data() {
    return {
      task_id: "",
      task_name: "", //
      architecture_type: "",  //
      task_status: "", //
      deploy_port: "",  //
      create_time: "", //
      namespace: "", //NAMESPACE
      version: "",  //
      task_details: [],
      step_info: [],
      clearips:"", //ip
      retryips:"", //ip
      warmMsg: "",  //
      allWarmMsg: "",   //
      errormsg:"",   //
      infolist:0
    }
  },
  filters: {
    toStatusText: function (value) {
      return map[value] || ''
    },
    toStepstatusText: function (value) {
      return status[value] || ''
    },
    toMsg:function (value) {
      var msg =value.replace(';',';\n')
      // console.log(msg)
      return msg
    },
  },
  mounted() {
    this.getInterval();
    this.getMysqlInfo(this.task_id);
  },
  updated: function () {
    var i = 0;
    var li = document.getElementsByName("li");
    for (i = 0; i < li.length; i++) {
      li[i].onclick = function () {
        for (var i = 0; i < li.length; i++) {
          li[i].classList.remove("li");
        }
        this.classList.add("li")
      }
    }
    // console.log(this.step_info)
  },
  methods: {
    getInterval(){
      var that = this;
      if (this.$route.query.task_id) {
        this.task_id = this.$route.query.task_id;
        var timer = setInterval(function () {
          that.getMysqlInfo(that.task_id);
          // console.log(that.task_status);
          if (that.task_status == 1 || that.task_status == 2 || that.task_status == 6) {
            clearInterval(timer);
          }
        }, 1000)
      }
    },
    getMysqlInfo: function (task_id) {    //
      var param = "?task_id=" + task_id;
      this.$http.get('/acmeCloud/deploy/mysql/detail' + param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 
          this.task_id = response_info.data.task_id;
          this.task_name = response_info.data.task_name;
          this.architecture_type = response_info.data.architecture_type;
          this.task_status = response_info.data.task_status;
          this.deploy_port = response_info.data.deploy_port;
          this.create_time = response_info.data.create_time;
          this.namespace = response_info.data.namespace;
          this.version = response_info.data.version;
          this.task_details = response_info.data.task_details;
          if (this.task_details.length > 0) {
            this.step_info = this.task_details[this.infolist].step_info
          }
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    deployhalt: function (instance_ip) {   // 
      let data = {
        "task_id": this.task_id,
        "instance_ip": instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/deployhalt',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    },
    restart: function (instance_ip) {  //  
      let data = {
        "task_id": this.task_id,
        "instance_ip": instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/restart',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    },
    skip: function (instance_ip) { // 
      // console.log("")
      let data = {
        task_id: this.task_id,
        instance_ip: instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/skip',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 
          // console.log("")
          this.$notify({
            type: 'error',
            message: 'DBA'
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
    retry: function () {  //
      // console.log("")
      let data = {
        task_id: this.task_id,
        instance_ip: this.retryips,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/retry',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 
          this.errorMsg=response_info.msg
          this.$notify({
            type: 'success',
            message: this.errorMsg
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
    clean: function () {  //
      // console.log("")
      let data = {
        task_id: this.task_id,
        instance_ip: this.clearips,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/clean',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    },
    warm: function () {  //
      var param = "?task_id=" + this.task_id;
      this.$http.get('/acmeCloud/deploy/mysql/warn' + param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 
          // console.log("")
          this.warmMsg = response_info.data
          // console.log(response_info.data)
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    allDeployhalt: function () {  //
      let data = {
        "task_id": this.task_id,
        "instance_ip": "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/deployhalt',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    },
    allRetry: function () { //
      // console.log("")
      let data = {
        task_id: this.task_id,
        instance_ip: "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/retry',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    },
    allClean: function () {  //
      // console.log("")
      let data = {
        task_id: this.task_id,
        instance_ip: "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/clean',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//
        } else {
          // 

        }
      }, function (res) {
        // console.log(res)
      });
    }
  }
}



