var map = {
  "0":"",
  "1":""
}
export default {
  data() {
    return {
      agentSshPort_tips:'',
      agentSshPassword_tips:'',
      agentSshUser_tips:'',
      entryTitle_tips:'',
      host_message_tips:'',
      is_login: this.$cookies.get("is_login"),
      offAndOn: true, //true false 
      isEntrylastlog:true,  //falsetrue
      agentSshUser:"",       //
      agentSshPassword:"",   //
      agentSshPort:22,  //SSH 
      entryTitle:"",
      hostMessage:"",
      hostList:[],
      entrylastlogList:[],  // 
      total: 0,       //
      currentPage: 1,   //
      current: this.currentPage,  //
      entryhistorylogList:[],    //
      isInfo:-1,    //
      // host_ip:""
    }
  },
  filters: {
    toHostText:function (value) {
      return map[value] || ''
    },
  },
  mounted(){
    this.getEntrylastlog();
    this.setCurrent(1);
    var i=0;
    var agentli=document.getElementsByName("agentli");
    for (i=0;i<agentli.length;i++){
      agentli[i].onclick=function () {
        for (i=0;i<agentli.length;i++){
          agentli[i].classList.remove("agentli")
        }
        this.classList.add("agentli");
      }
    }
  },
  props: {
    // total: {// 
    //   type: Number,
    //   default: 10
    // },
    display: {// 
      type: Number,
      default: 10
    },
    // currentPage: {// 
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods:{
    hostEntry:function(){    //
      if(this.hostMessage == ''){
        this.host_message_tips ='ip';
      }
      if(this.entryTitle ==''){
        this.entryTitle_tips='';
      }
      var isAgentSwitch='';
      if(this.offAndOn){//
        isAgentSwitch=0;
        if(this.agentSshPassword ==''){
          this.agentSshPassword_tips ='';
        }
        if(this.agentSshUser ==''){
          this.agentSshUser_tips =''
        }
        $(window).scrollTop(0);
        if(this.entryTitle&&this.agentSshPassword&&this.agentSshUser !=''){
          if(this.hostMessage != ','){
            let data={
              host_list:this.hostMessage,
              agent_switch:isAgentSwitch,
              agent_ssh_user:this.agentSshUser,
              agent_ssh_password:this.agentSshPassword,
              agent_ssh_port:parseInt(this.agentSshPort),
              entry_title:this.entryTitle
            };
            // console.log(data);
            this.post_entry(data);
          }
        }
      }else{//
        isAgentSwitch=1;
        if(this.hostMessage != ','){
            if(this.entryTitle_tips !=''){
              let data={
                host_list:this.hostList,
                agent_switch:isAgentSwitch,
                agent_ssh_user:this.agentSshUser,
                agent_ssh_password:this.agentSshPassword,
                agent_ssh_port:parseInt(this.agentSshPort),
                entry_title:this.entryTitle
              };
              this.post_entry(data);
            }
          }
        }
    },
    post_entry(e){
      this.$http.post('/acmeCloud/host/entry',
        JSON.stringify(e), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          // alert("!");
          // console.log('');
          var entryId=response_info.data;
          // console.log(response_info.data);
          this.$router.replace({ path: 'entryDetails?'+'id='+response_info.data});
        } else {
          if(response_info.status==1001){
            alert("ip!")
          }
          if(response_info.status==2001){
            alert("")
          }
          if(response_info.status==2002){
            alert("")
          }
          if(response_info.status==2003){
            alert("")
          }
          if(response_info.status==2001){
            alert("")
          }
          // 
          // alert("!")
        }
      });
    },
    //
    getEntrylastlog:function(entryId){
        var param='?entryId='+entryId;
        this.$http.get('/acmeCloud/host/entrylastlog'+param).then(function (res) {
         // console.log(res)
          var  response_info = res.body;
         if(response_info.status==0){
            this.entrylastlogList=response_info.data;
            this.entryhistorylogList=[];
         }else{

         }
        })
    },
    //
    getEntrylog:function () {
      //
      this.$http.get('/acmeCloud/host/entry/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 
          this.total = response_info.data;
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
      //   
      let data = {
        "currentPage": this.currentPage,
        "pageSize": this.display
      }
      // console.log(data)
      this.$http.get('/acmeCloud/host/entryhistorylog?'+
        "currentPage="+this.currentPage+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 
          this.entrylastlogList=[];
          this.entryhistorylogList = response_info.data;
        } else {
          // 
          // console.log("...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    // 
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.currentPage = this.current;
        this.$emit('pagechange', this.current);
      }
    }
  }
}



