    for(var i=0;i<that.YX.length;i++) {
      for (var a = 0; a < that.ddlList.length; a++) {
        // console.log(a)
        if (that.YX[i].text == that.ddlList[a].optype) {
          that.a0[a] = i;
        }
      }
    }
    that.a0.push('')
    // let data={//DDL
