export default {
  data(){
    return{
      entry_list:[],
      total: 0,
      current_page: 1,
      current: 1,
    }
  },
  mounted(){
    this.setCurrent(1);
    // this.get_data();//
    // this.get_page_num();//
  },
  props: {
    display: {// 
      type: Number,
      default: 10
    },
    pagegroup: {// 
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods:{
    // 
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      window.scrollTo(0,0);
      //
      this.$http.get('/acmeCloud/host/entry/total').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {this.total = response_info.data; } else {
        }
      }, function (res) {
        // console.log(res)
      });
      //   
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      this.$http.get('/acmeCloud/host/entry/list?'+
        "current_page="+this.current_page+"&page_size="+this.display).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // 
          this.entry_list = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
          // 
          // console.log("...")
        }
      });
    },

  }
}



